import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';

import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import { TableData, ColEditDelete } from 'components/common';
import Btn from 'components/common/button/Btn';
import { getQueryParams } from 'utils/urlUtil';
// import { getBonusNotes } from 'services/BNnote';
import { getListAddReward, exportDataAddReward } from 'services/AddReward';
import FormFilterReward from './FormFilterReward';

class AddRewardPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {
      semesterId: null,
    },
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };
  dataFormSeach = data => {
    this.setState({
      paramSearchClass: data,
      reload: this.state.reload + 1,
    });
  };

  exportAddReward = () => {
    const { paramSearchClass } = this.state;
    exportDataAddReward(paramSearchClass);
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số QĐ',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngày QĐ',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
      },
      {
        title: 'Cấp khen thưởng',
        dataIndex: 'parentBonusTypeLabel',
        key: 'parentBonusTypeLabel',
      },
      {
        title: 'Hình thức',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Loại khen thưởng',
        dataIndex: 'bonusTypeLabel',
        key: 'bonusTypeLabel',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#reward/updateForm/${record.approvalId}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
            hidenBtnDelete="true"
          />
        ),
      },
    ];
    const { reload, paramSearchClass } = this.state;
    return (
      <Card title="Nhập khen thưởng">
        <Row>
          <Col span={24}>
            <FormFilterReward dataForm={this.dataFormSeach} />
          </Col>
        </Row>
        <Row>
          <BtnAddNew href="#/reward/addForm" />
          {paramSearchClass.semesterId ? (
            <Btn
              style={{ marginLeft: 20, height: 35 }}
              onClick={this.exportAddReward}
              icon="download"
            >
              Download
            </Btn>
          ) : null}
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getListAddReward}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AddRewardPage />
  </Page>
);



